.photo-list {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__container {
    display: grid;
    grid-template-columns: repeat(5, minmax(200px, 1fr));
    column-gap: 30px;
    row-gap: 30px;
    margin-left: 70px;
    margin-right: 70px;
    list-style: none;
    padding: 0;
  }

  &__error {
    font-weight: 500;
    font-size: 30px;
    line-height: 38px;
    color: darkred;
    text-align: center;
    margin: 0;
  }
}

.photo-list__buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  margin-top: 40px;

  &__page {
    margin: 0;
    font-weight: 500;
    font-size: 20px;
    line-height: 28px;
  }

}

@media screen and (max-width: 1200px) {
  .photo-list__container {
    grid-template-columns: repeat(4, minmax(230px, 1fr));
    row-gap: 47px;
    margin-left: 30px;
    margin-right: 30px;
  }
}

@media screen and (max-width: 1050px) {
  .photo-list__container {
    grid-template-columns: repeat(3, minmax(250px, 1fr));
  }
}

@media screen and (max-width: 870px) {
  .photo-list__container {
    grid-template-columns: repeat(2, minmax(250px, 1fr));
  }
}

@media screen and (max-width: 550px) {
  .photo-list__container {
    grid-template-columns: repeat(2, minmax(200px, 1fr));
    row-gap: 39px;
    margin-left: 10px;
    margin-right: 10px;

    &__error {
      font-weight: 500;
      font-size: 20px;
      line-height: 28px;
    }
  }

  .photo-list__buttons {
    margin-top: 20px;
  }
}

@media screen and (max-width: 460px) {
  .photo-list__container {
    grid-template-columns: repeat(1, minmax(250px, 1fr));
  }
}